.home {
  display: grid;
  min-width: 200px;

  width: 100vw;

  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: (1fr, 1fr, 1fr, 1fr, 1fr);
  gap: 3%;
  grid-template-areas:
    "  header header header header header header header header  "
    "  . projet-flag  projet-3 . navigation .  contact . "
    "   . projet-2  projet-4 . . .  . .   "
    "  . .  . . .  .  . .  ";

  .navbar {
    height: 100px;
    background-color: rgb(233, 228, 228);
    border: solid 1px rgb(233, 228, 228);
    border-radius: 10px;

    .social-network {
      width: 20%;
      font-size: 1.4rem;
      .color-social {
        color: #3b5998;
      }
      .color-lin {
        color: #0e76a8;
      }

      @media screen and (max-width: 650px) {
        width: 30%;
        margin-right: -30%;
      }

      ul {
        display: flex;
        width: 100%;
        min-width: 100px;
        justify-content: space-around;

        @media screen and (max-width: 450px) {
          min-width: 100%;
        }

        a {
          padding: 1rem;
          transition: 1.1s ease-out;
        }
      }
    }

    .div-block {
      position: absolute;
      width: 140px;
      right: 20%;
      top: 40px;
    }
    H1 {
      color: black;
      display: block;
      padding: 8px 5px 5px 8px;
      margin-top: 30px;
      border: solid 2px rgb(255, 255, 255);
      border-radius: 25px;
      background-color: rgb(250, 250, 250);
      box-shadow: 0px 5px 6px rgb(203, 199, 199);
      align-items: center;
    }

    span {
      display: block;
      font-weight: 900;
      font-size: large;
    }
  }
  li {
    color: $texte;
  }
  .en-cours {
    color: black;
  }
  .header-home {
    color: white;
    font-size: 2rem;
  }
  .title-cv {
    color: $initial-color;
  }

  :hover .lien-cv {
    color: orange;
  }

  .button-home {
    height: 100px;
    width: 200px;
    text-align: center;
    cursor: pointer;
    box-sizing: border-box;
    box-shadow: 0 3px 8px rgb(84, 187, 113);
    text-decoration: none;
    font-weight: bold;
    text-transform: uppercase;
    background: rgb(84, 187, 113);
    color: #b4d0ce !important;
    display: inline-block;
    border-radius: 19px;
    transition: all 0.3s ease-in 0s;

    padding-right: 30px;
  }
  .button-home:hover {
    text-decoration: none;
    background-color: rgb(50, 62, 65);
    color: rgb(238, 224, 224) !important;
    box-shadow: 0 3px 8px rgb(50, 62, 65);
  }
  .projet-flag {
    grid-area: projet-flag;
    font-size: 2rem;
  }
  .projet-2 {
    grid-area: projet-2;
    font-size: 2rem;
  }
  .projet-3 {
    grid-area: projet-3;
    font-size: 2rem;
  }
  .projet-4 {
    grid-area: projet-4;
    font-size: 2rem;
  }
  .contact1 {
    grid-area: contact;
    font-size: 2rem;
    min-width: 20px;
    max-width: 1300px;
  }
  .home-proj {
    font-size: 1rem;
  }

  .header {
    grid-area: header;
    text-align: center;
  }
  .nav-link {
    color: $texte;
  }
  .navigation {
    font-size: 1.4rem;
    line-height: 28px;
    color: $color-3;

    &:before {
      content: "";
      grid-area: navigation;
      height: 100px;
      width: 100px;
      border-radius: 50%;
      background: $color-3;
      z-index: -1;
      animation: navAnim 20s infinite alternate;

      @keyframes navAnim {
        50% {
          transform: scale(3.2);
        }
        100% {
          transform: scale(1);
        }
      }
      @media screen and (max-width: 430px) {
        height: 50px;
        width: 50px;
      }
    }
    .nav-active1 {
      color: $black;
    }
    ul:hover li:not(:hover) {
      opacity: 0.5;
    }
    ul:hover li:hover {
      color: orange;
    }
  }
}
